.masterContainer {
	margin-left: 2%;
	margin-right: 2%;

	transition: -webkit-transform 0.7s cubic-bezier(0.34, 1.56, 0.64, 1);

	transition: transform 0.7s cubic-bezier(0.34, 1.56, 0.64, 1);

	transition: transform 0.7s cubic-bezier(0.34, 1.56, 0.64, 1), -webkit-transform 0.7s cubic-bezier(0.34, 1.56, 0.64, 1);
	margin-bottom: 1rem;
}
.masterContainer .text {
	text-align: center;
}
.masterContainer:hover {
	-webkit-transform: scale(1.1, 1.1) !important;
	        transform: scale(1.1, 1.1) !important;
}
.masterContainer .text {
	color: white;
	font-family: "Bebas Neue";
	margin-top: 1%;
}

.graphContainer {
	background-color: rgba(180, 180, 180, 0.78);
