	.masterContainer .text {
		text-shadow:
			-1px -1px 0 #000,  
			1px -1px 0 #000,
			-1px 1px 0 #000,
			1px 1px 0 #000;
	}
	.masterContainer:hover {
		-webkit-transform: none !important;
		        transform: none !important;
	}

	.flexDiv {
		width: 100%;
	
		display: flex;
