.linkBlock {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: space-around;
    align-items: center;

    text-align: center;

    font-size: 25px;
}

.linkBlockWrapper .container {
    width: 30rem;
    height: 10rem;
    min-height: 100px;
    box-shadow: 0 0 1rem 0 rgba(0, 0, 0, .3); 
    border-radius: 10px;
    position: relative;
    z-index: 1;
    background: inherit;
    overflow: hidden;
    margin: 50px;
}

.linkBlockWrapper .container:before {
    content: "";
    position: absolute;
    background: inherit;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    box-shadow: inset 0 0 2000px rgba(255, 255, 255, 0.6);
    -webkit-filter: blur(10px);
            filter: blur(10px);
    margin: -20px;
}

.linkBlockWrapper .container a {
    height: 45px;
    width: 45px;
    font-size: 45px;

    transition: color 0.2s ease-out, -webkit-transform 0.2s ease-in-out;

    transition: color 0.2s ease-out, transform 0.2s ease-in-out;

    transition: color 0.2s ease-out, transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
}
.linkBlockWrapper .container a:link {
    text-decoration: none;
    color: rgb(0, 0, 0);
}
.linkBlockWrapper .container a:visited {
    text-decoration: none;
    color: #2c0e47;
}
.linkBlockWrapper .container a:hover {
    -webkit-transform: scale(1.6, 1.6);
            transform: scale(1.6, 1.6);
    color: rgb(52, 41, 204);
}

